.calendar-block {
    position: relative;
}

.old-calendar-block {
    width: 100%;
    position: absolute;
    top: 0;
    left: 0;
}

.calendar-block-animate {
    height:150px;
    background:#EEE;
    opacity: 0;
    -webkit-animation: calendarRoll .7s ease;
            animation: calendarRoll .7s ease;
}

@-webkit-keyframes calendarRoll {
    0% {
        opacity:1;
        -webkit-transform: rotateX(30deg);
                transform: rotateX(30deg);
    }
    25%{
        opacity:0.9;
        -webkit-transform:translate3d(0, -40px, 0)  rotateX(60deg);
                transform:translate3d(0, -40px, 0)  rotateX(60deg)
    }
    50% {
        opacity:0.7;
        -webkit-transform: translate3d(0, -105px, 0) rotateX(120deg);
                transform: translate3d(0, -105px, 0) rotateX(120deg)
    }
    75% {
        opacity:0.5;
        -webkit-transform: translate3d(0, -125px, 0) rotateX(130deg);
                transform: translate3d(0, -125px, 0) rotateX(130deg)
    }
    100% {
        opacity:0;
        -webkit-transform: translate3d(0, -125px, 0) rotateX(140deg);
                transform: translate3d(0, -125px, 0) rotateX(140deg)
    }
}

@keyframes calendarRoll {
    0% {
        opacity:1;
        -webkit-transform: rotateX(30deg);
                transform: rotateX(30deg);
    }
    25%{
        opacity:0.9;
        -webkit-transform:translate3d(0, -40px, 0)  rotateX(60deg);
                transform:translate3d(0, -40px, 0)  rotateX(60deg)
    }
    50% {
        opacity:0.7;
        -webkit-transform: translate3d(0, -105px, 0) rotateX(120deg);
                transform: translate3d(0, -105px, 0) rotateX(120deg)
    }
    75% {
        opacity:0.5;
        -webkit-transform: translate3d(0, -125px, 0) rotateX(130deg);
                transform: translate3d(0, -125px, 0) rotateX(130deg)
    }
    100% {
        opacity:0;
        -webkit-transform: translate3d(0, -125px, 0) rotateX(140deg);
                transform: translate3d(0, -125px, 0) rotateX(140deg)
    }
}

