/*Holes in calendar*/
.calendar-block:before, .calendar-block:after {
    content: '';
    float: left;
    position: absolute;
    top: 5px;
    width: 8px;
    height: 8px;
    background: #111;
    z-index: 1;
    border-radius: 10px;
    box-shadow: 0 1px 1px #fff;
}

.calendar-block:before {
    left: 11px;
}

.calendar-block:after {
    right: 11px;
}
/*Rings in calendar*/
.calendar-block h3:before, .calendar-block h3:after {
    content: '';
    float: left;
    position: absolute;
    top: -5px;
    width: 4px;
    height: 14px;
    background: #dadada;
    background: -webkit-gradient(linear, left top, left bottom, from(#f1f1f1), to(#aaa));
    background: -moz-linear-gradient(top, #f1f1f1, #aaa);
    z-index: 2;
    border-radius: 2px;
}

.calendar-block h3:before {
    left: 13px;
}

.calendar-block h3:after {
    right: 13px;
}



/*LISTING EFFECT*/

.block-container::before,
.block-container::after {
    /*Add shadow to distinguish sheets from one another */
    box-shadow: 2px 1px 3px rgba(0, 0, 0, 0.2);
}

.block-container::before,
.block-container::after {
    content: "";
    position: absolute;
    width: 100%;
    height: 100%;
    background-color: #eee;
    background: -webkit-linear-gradient(top, #dd1818 27%, #DDD 27%);
    background: linear-gradient(to bottom, #dd1818 27%, #DDD 27%);
}

/*Second sheet of paper */

.block-container::before {
    left: 5px;
    top: 5px;
    z-index: -1;
}

/*Third sheet of paper */

.block-container::after {
    left: 10px;
    top: 10px;
    z-index: -2;
}

